import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import * as firebase from 'firebase';
import { UserService } from '../admin/adminShared/user.service';
import { Product } from '../admin/adminShared/product';

@Component({
    template: require('./shop.component.html'),
    styles: [require('./shop.component.css')]
})
export class ShopComponent implements OnInit {
    products: Product[];

    constructor( private userSVC: UserService, private router: Router ){} 

    ngOnInit(){
        this.getProducts();
    }

    getProducts(){
        let dbRef = firebase.database().ref('products/')
        dbRef.once('value')
            .then((snapshot)=> {
                let tmp: string[] = snapshot.val();
                this.products = Object.keys(tmp).map(key => tmp[key])
            });
    }

    chooseProduct(prod: Product) {
        this.router.navigate(['/product', prod.id]);
    }   

}


